import { Box, Button, Container } from '@material-ui/core'
import React, { useState } from 'react'
import { connect } from 'react-redux'
import LoginModal from '../Layout/LoginModal'
import * as classes from './Welcome.module.scss'
import { useTranslation } from 'react-i18next'

const Welcome = props => {
    const { t } = useTranslation()
    const [loginVisible, setLoginVisible] = useState(false)

    return props.account._id ? null : (
        <Container>
            <Box style={{ margin: '10% 0px' }}>
                <header className={classes.header}>
                    <h1 className={classes.header__title}>
                        <div>
                            <span>{t('welcome.title')}</span>
                        </div>
                        <div className={classes.slider}>
                            <ul className={classes.slider__words}>
                                <li className={classes.slider__word}>
                                    {t('welcome.phrase1')}
                                </li>
                                <li className={classes.slider__word}>
                                    {t('welcome.phrase2')}
                                </li>
                                <li className={classes.slider__word}>
                                    {t('welcome.phrase3')}
                                </li>
                                <li className={classes.slider__word}>
                                    {t('welcome.phrase4')}
                                </li>
                                <li className={classes.slider__word}>
                                    {t('welcome.phrase1')}
                                </li>
                            </ul>
                        </div>
                    </h1>
                </header>
            </Box>
            <span className={classes.subheader}>{t('welcome.subheader')}</span>
            <div className={classes.videoWrapper}>
                <iframe
                    width="560"
                    height="315"
                    src={
                        'https://www.youtube.com/embed/' +
                        t('welcome.videoLink')
                    }
                    frameBorder="0"
                    allowFullScreen
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                />
            </div>
            {!props.account._id && (
                <>
                    <Box
                        style={{
                            margin: '40px auto 10px',
                            textAlign: 'center',
                        }}
                    >
                        <Button
                            variant="contained"
                            color="primary"
                            size="large"
                            onClick={() => setLoginVisible(true)}
                        >
                            {t('welcome.button')}
                        </Button>
                    </Box>
                    <LoginModal
                        loginVisible={loginVisible}
                        setLoginVisible={setLoginVisible}
                    />
                </>
            )}
        </Container>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

export default connect(mapStateToProps)(Welcome)
